.order {
    padding: 40px;
    margin: 20px 0;
    border: 1px solid lightgray;
    background-color: white;
    position: relative;
    /* making the parent div relative so that the onside element stays inside */
}

.order_id {
    position: absolute;
    top: 40px;
    right: 20px;
}

.order_total {
    font-weight: 500;
    text-align: right;
}